@import url('https://fonts.googleapis.com/css2?family=Advent+Pro:wght@200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Glass+Antiqua&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poiret+One&display=swap');

body {
    font-family: 'Roboto Mono', sans-serif;
}

#header {
    background-color: #3c2c3e;
    height: auto;
    text-align: center;
}

#header hr {
    background: #fff;
    height: 1px;
}

#header h5 {
    color: #fff;
    font-family: 'Poiret One', cursive;
    font-size: 1.1rem;
    font-style: italic;
    font-weight: 900;
    letter-spacing: 4px;
    line-height: 2;
    text-transform: capitalize; 
}

#header h1 {
    color: #fff;
    font-family: 'Advent Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: 300;
}

h1 {
    font-family: 'Advent Pro', sans-serif;
    font-size: 1.8rem;
    font-weight: 300;
}

#header img {
    width: 60%;
}

#header form { 
    padding: 100px 0;
}

.form input.form-control {
    background: transparent;
    letter-spacing: 2px;
}

.form .input-group-text {
    background-color: #3c2c3e;
    color: #fff;
    font-family: 'Pacifico', cursive;
    font-family: 'Advent Pro', sans-serif;
    font-size: 1rem;
    width: 100px;
}

.form ::placeholder {
    color: #fff;
    font-size: 0.8rem;
    font-weight: 200;
}

.form .radio-div label input {
    margin-right: 5px;
}

@media screen and (max-width: 768px) {
    #header h1 {
        font-size: 1.5rem;
    }

    #header h5 {
        font-size: 0.8rem;
    }

    #header img {
        width: 100%;
    }
  }

#assessment-page {
    text-align: center;
}

#assessment-page h1 {
    font-size: 2.5rem;
}

#inputState {
    background: "transparent";
    color: #000;
    font-family: 'Roboto Mono', sans-serif;
    font-size: 0.8rem;
    font-weight: 300;
    height: auto;
}

@media screen and (max-width: 768px) {
    #assessment-page h1 {
        font-size: 2rem;
    }
    #assessment-page p {
        font-size: 1rem;
    }
  }

  #display-page {
    height: auto;
    text-align: center;
  }

  #display-page h1 {
    font-size: 2.5rem;
  }

  #display-page img {
    width: 40%;
  }

  @media screen and (max-width: 768px) {
    #display-page h1 {
        font-size: 2rem;
      }
    
      #display-page img {
        width: 100%;
      }
  }